$yellow: #f1ac11;
//VARIABLES^^^
footer {
  width: 100%;
  padding-top: 80px;
  padding-bottom: 80px;
  background-image: url("../images/footerBack.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  justify-content: space-around;
  align-items: baseline;
  flex-wrap: wrap;
  &::before {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    background-color: rgba(1, 1, 1, 0.7);
    top: 0;
    left: 0;
  }

  .links {
    h3 {
      color: #fff;
      font-size: 1.5rem;
      text-transform: uppercase;
      text-align: left;
      margin-bottom: 28px;
      font-style: italic;
      font-weight: 400;
    }
    ul {
      li {
        margin-bottom: 2px;
        a {
          color: #bbb;
          text-transform: uppercase;
          &:hover {cursor: pointer;}
          text-decoration: none;
          font-size: 1.2rem;
        }
      }
    }
    form {
      input {
        width: 220px;
        height: 32px;
        border: none;
        font-size: 1rem;
        color: #fff;
        background-color: rgba(255, 255, 255, 0.3);
        padding-left: 15px;
        margin-right: 25px;
      }
      input::-webkit-input-placeholder {color: #fff};
      input::-moz-placeholder {color: #fff};
      input:-moz-placeholder {color: #fff};
      input:-ms-input-placeholder {color: #fff};

      button.shaded-btn {
        position: relative;
        height: 30px;
        transition: 0.2s;
        font-size: 1.2rem;
        padding-right: 20px;
        padding-left: 20px;
        border: none;
        background-color: $yellow;
        font-style: italic;
        border-radius: 2px;
        box-shadow: 0 3px 0 0 #b47d00;

        &:hover {
          cursor: pointer;
        }
        &:focus {

          box-shadow: none;
          border: none;
        }
      }
    }
  }
}
@media screen and (max-width: 900px) {
  footer {
    padding-left: 80px;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;

    .links {
      margin-bottom: 50px;
    }
  }
}
@media screen and (max-width: 450px) {
  footer {
    padding-left: 50px;
    .links {
      input {
        margin-bottom: 20px;
      }
    }
  }
}
